/*--------------------------------------------------------------------*/

/* Style nav bar */
.nav {
    height: 5vh;
    display: flex;
    align-items: center;
    background-color: #716F81;
    padding: 1vh 1vw;
    box-shadow: 1px 0 3px black;
    margin-bottom: 0;
} 

/* Button back to homepage */ 
.viewcards-header {
    margin: 0;
    font-size: 24px;
    color: white;
}

/* TigerFoodies homepage link turns bold on hover */
.viewcards-header a:hover {
    font-weight: bold;
}

.nav a {
    text-decoration: none;
}

/*--------------------------------------------------------------------*/

/* Style page name */
.page-name {
    margin-top: 3vh;
    margin-bottom: 3vh;
}

/* Style the main content layout */
.viewcards-main {
    display: flex;
    flex-direction: column;
    height: 88vh;
    width: 100%;
    overflow-y: auto;
}

.viewcards-container {
    display: flex;
    flex-direction: column;
    height: 100%;
}

/* Style list of cards */
.viewcards-card-list {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    gap: 1vw;
    padding: 1vh 1vw;
    width: 98vw;
}

/* Style edit and delete buttons */
.card-actions {
    display: flex;
    gap: 4vw;
    margin-top: 10px;
    margin-bottom: 3vh;
    justify-content: center;
}

.edit-button, .delete-button {
    font-size: 0.9em;
    border: none;
    border-radius: 20px; /* Rounded buttons */
    cursor: pointer;
    background-color: #F6AE99;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
    padding: 1vh 1vw;
}

.edit-button:hover {
    background-color: #E79781;
    color: white /* Green text for Edit */
}

.delete-button:hover {
    background-color: #E79781;
    color: white;
}

/*--------------------------------------------------------------------*/

/* Media query for screens between 840px and 1240px */
@media screen and (max-width: 1280px) {
    /* On a smaller screen, less cards appear in one row */
    .viewcards-card-list {
        grid-template-columns: repeat(4, 1fr);
    }
}

/*--------------------------------------------------------------------*/

/* Media query for screens between 480px and 840px */
@media screen and (max-width: 840px) {
    /* On a smaller screen, less cards appear in one row */
    .viewcards-card-list {
        grid-template-columns: repeat(2, 1fr);
    }

    /* Make gap between edit and delete button larger on smaller screen */
    .card-actions {
    gap: 15vw;
    }
}

/*--------------------------------------------------------------------*/

/* Media query for screens between 0px and 480px */
@media screen and (max-width: 460px) {
    /* On a smaller screen, less cards appear in one row */
    .viewcards-card-list {
    grid-template-columns: repeat(1, 1fr);
    }

    /* Make gap between edit and delete button larger on mobile */
    .card-actions {
    gap: 40vw;
    }
}

/*--------------------------------------------------------------------*/